* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}
ul {
  list-style-type: none;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.country-list {
  margin: 0 0 0 -40px;
}
.country-list li:not(:last-child) {
  margin-bottom: 10px;
}
.country-list__item {
  display: flex;
  align-items: center;
}
.country-list__item p {
  margin: 0 0 0 15px;
}
.country-info__item {
  display: flex;
  align-items: center;
  /* padding-left: 40px; */
}
.country-info li:not(:last-child) {
  margin-bottom: 10px;
}
.country-info img {
  margin-right: 15px;
}

.country-info li {
  margin-left: -40px;
}
.country-info h1 {
  margin: 0;
}
